import UserList from "./User/UserList"
import EventList from "./Event/EventList"
import Svitimovi from "./Timovi/SviTimovi"
import TimDetalji from "./Timovi/TimDetalji"
import UserForm from "./User/UserForm"
import EventForm from "./Event/EventForm"
import UserDetails from "./User/UserDetails"
import EventDetails from "./Event/EventDetails"
import NotFound from "..//NotFound.js";
import styled from "styled-components"
import Navbar from "./Navbar"
import React, { useEffect, useState } from "react"
import { Route, Switch } from "react-router-dom";
import { UserlistContext } from "./Context/UserlContext"

const Global = styled.div`
    display: flex;
    flex-direction: column;
    min-height: 100vh;
`;

const LoggedIn = () => {

	const [ userlist, setUserlist ] = useState([])

  	useEffect(() => {
		  console.log("FETCH")
  	  	fetch("/get_users")
  	  	.then(res => res.json())
  	  	.then(data => setUserlist(data))
  	  	.catch(err => console.error(err))
  	}, [])

  	return (
		  <Global>
            <Navbar />
			<UserlistContext.Provider value={{userlist, setUserlist}}>
                <Switch>
                <Route exact path="/"><UserList /></Route>              
                <Route path="/eventi"><EventList /></Route>
                <Route path="/timovi"><Svitimovi /></Route>
                <Route path="/tim/:id" render={(props) => <TimDetalji {...props} />}/>
                <Route path="/korisnik_obrazac/:id?" render={(props) => <UserForm {...props} />}/>
                <Route path="/event_obrazac/:id?" render={(props) => <EventForm {...props} />}/>
                <Route path="/korisnik/:id" render={(props) => <UserDetails {...props} />}/>
                <Route path="/event/:id" render={(props) => <EventDetails {...props} />}/>
                <Route><NotFound /></Route>
              </Switch>
			</UserlistContext.Provider>
        </Global>
  );
};

export default LoggedIn;
