import { useState, useEffect, useContext } from "react";
import { useParams } from "react-router-dom"
import styled from "styled-components";
import { UserlistContext } from "../Context/UserlContext";

const Formstyled = styled.form`
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    font-size: 4rem;
`;

const Addedclan = styled.div`
    ${({ active }) => active && `
        background-color: blue
    `}
`;

const Eventform = () => {

    const { id } = useParams()

    const { userlist } = useContext(UserlistContext)
    
    const [ filtered, setFiltered ] = useState([])

    const [ eventinfo, setEventinfo ] = useState({
        ime: "",
        datum: "",
        cijena: "",
        tim: "",
    })

    const [ clanovi, setClanovi ] = useState([])

    const timovi = [ "HR tim", "Party tim", "Partnership tim", "Social Inclusion tim", "HealthAndWellbeing tim", 
                     "ICE tim", "Buddy tim", "PR tim", "Travel tim", "Grant tim" ]

    const { ime, datum, cijena, tim } = eventinfo

    useEffect(() => {
        setFiltered(userlist)
        if (id) {
            fetch("/get_eventform", {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ id }),
              })
            .then(res => res.json())
            .then(data => {
                setEventinfo(data)
                if (data.json_agg[0]) {
                    setClanovi(data.json_agg)
                }
            })
            .catch(err => console.error(err))
        }
    }, [userlist])

    function handleBox(e) {
        setEventinfo(prevInfo => {
            return { ...prevInfo, [e.target.name]: e.target.value}
        })
    }

    function pushClan(e) {
        const clan_id = Number(e.target.getAttribute("name"))

        if (clanovi.includes(clan_id)) {
            setClanovi(clanovi.filter(clan => clan !== clan_id));

        } else {
            setClanovi(prevInfo => [ ...prevInfo, clan_id ])
        }
    }
    
    function sendEventinfo(e) {
        e.preventDefault()

        const info = [ eventinfo, clanovi ] 

        if (id) {
            fetch("/update_event", {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(info),
              })
              .catch(err => console.error(err))
        } else {
            fetch("/create_event", {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(info),
              })
              .catch(err => console.error(err))
        }
        window.location.href = "/eventi" 
    }

    function Searchbar(e) {
        const search = userlist.filter(elem => {
            const string = elem.ime + " " + elem.prezime
            return string.toLowerCase().includes(e.target.value) || 
                   string.toUpperCase().includes(e.target.value)
        })

        setFiltered(search)
    }

    return (
        <Formstyled onSubmit={sendEventinfo}>
            <div>
                <label htmlFor="ime">Ime:</label>
                <input onChange={handleBox} type="text" id="ime" name="ime" value={ime}/>

                <label htmlFor="cijena">Cijena:</label>
                <input onChange={handleBox} type="number" id="cijena" name="cijena" value={cijena}/>
            </div>

            <div>
                <label htmlFor="datum">Datum rođenja:</label>
                <input onChange={handleBox} type="date" id="datum" name="datum" value={datum}/>
            </div>

            <div>
                <label htmlFor="tim">Odaberi tim::</label>

                <select value={tim || "default"} onChange={handleBox} name="tim" id="tim">                
                    <option value="default" disabled> -- odaberi opciju -- </option>
                    {timovi.map(tim => <option value={tim}>{tim}</option> )}
                </select>
            </div>

            <input onChange={Searchbar} type="search" placeholder="Pretraži..." />

            <div>
            {filtered && filtered.length ? filtered.map(user => {
                return (
                    <Addedclan 
                        active={clanovi.includes(user.id)} 
                        onClick={pushClan} 
                        name={user.id} 
                        key={user.id}
                    >
                        {user.ime} {user.prezime} {user.tim} {user.tel}
                    </Addedclan>
                )
            })
            :
            <div>
                Nema članova za prikazati...
            </div>
            }
            </div>

            <input value="Spremi" type="Submit"/>
        </Formstyled>
    )
}

export default Eventform;
