import { useState, useEffect, useContext } from "react"
import { Link } from "react-router-dom"
import styled from "styled-components"
import { UserlistContext } from "../Context/UserlContext";

const Wrapper = styled.div`
    font-size: 5rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(115, 190, 255, 0.3);
`;

const Formlink = styled(Link)`
    margin: 1rem;
    padding: 1rem;
    text-decoration: none;
    color: white;
    background-color: cornflowerblue;
`;

const Input = styled.input`
    width: 50%;
    outline: none;
    border: none;
    border-bottom: solid black;
`;

const Alldiv = styled.div`
    width: 100%;
`;

const Userdiv = styled.div`
    font-weight: bolder;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem;
    background: #666;
    @media (min-width: 726px) {
        margin-left: 25%;
        margin-right: 25%;
    }
    :hover {
        box-shadow: 0 5px 10px black, 0 15px 40px gray;
    }
`;

const Styledlink = styled(Link)`
    font-size: 2rem;
    text-decoration: none;
    padding: 2rem;
    color: #003580;
`;

const Buttondivs = styled.div`
    display: flex;
    justify-content: center;  
`;

const Button = styled(Link)`
    box-shadow: 0 5px 10px black, 0 15px 40px gray;
    font-size: 1.5rem;
    text-decoration: none;
    border: none;
    margin: 2rem;
    border-radius: 1rem;
    padding: 1rem;
    color: black;
    background: #009fe3;
    :hover {
        background: rgb(222, 130, 44);
    }
`;

const Userlist = () => {

    const { userlist, setUserlist } = useContext(UserlistContext);

    const [ filtered, setFiltered ] = useState()

    useEffect(() => setFiltered(userlist), [userlist])

    function deleteUser(e) {
        const id = e.target.name
        
        fetch("/delete_user", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({id})
        })

        userlist.forEach((e, index) => {
            if (e.id == id) {
                userlist.splice(index, 1)
                setUserlist([...userlist])
            }
        });
    }

    function Searchbar(e) {
        const search = userlist.filter(elem => {
            const string = `${elem.ime} ${elem.prezime}`
            return string.toLowerCase().includes(e.target.value) || string.toUpperCase().includes(e.target.value)
        })
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Formlink to="korisnik_obrazac">Dodaj člana +</Formlink>
            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />
            <Alldiv>
            {filtered && filtered.length ? filtered.map(user => {
                return (
                    <Userdiv key={user.id}>
                        <Styledlink to={{pathname: `/korisnik/${user.id}`}}>
                            {user.ime} {user.prezime}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{pathname: `/korisnik_obrazac/${user.id}`}}>Ažuriraj</Button>
                            <Button to="" onClick={deleteUser} name={user.id}>Izbriši</Button>
                        </Buttondivs>
                    </Userdiv>
                )
            })
            :
            <Userdiv>
                Nema članova za prikazati...
            </Userdiv>
        }
        </Alldiv>
        </Wrapper>
    )
};

export default Userlist;
