import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import styled from "styled-components"

const Wrapper = styled.div`
    font-size: 5rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(115, 190, 255, 0.3);
`;

const Formlink = styled(Link)`
    margin: 1rem;
    padding: 1rem;
    text-decoration: none;
    color: white;
    background-color: cornflowerblue;
`;

const Input = styled.input`
    width: 50%;
    outline: none;
    border: none;
    border-bottom: solid black;
`;

const Alldiv = styled.div`
    width: 100%;
`;

const Eventdiv = styled.div`
    font-weight: bolder;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem;
    background: #666;
    @media (min-width: 726px) {
        margin-left: 25%;
        margin-right: 25%;
    }
    :hover {
        box-shadow: 0 5px 10px black, 0 15px 40px gray;
    }
`;

const Styledlink = styled(Link)`
    font-size: 2rem;
    text-decoration: none;
    padding: 2rem;
    color: #003580;
`;

const Buttondivs = styled.div`
    display: flex;
    justify-content: center;  
`;

const Button = styled(Link)`
    box-shadow: 0 5px 10px black, 0 15px 40px gray;
    font-size: 1.5rem;
    text-decoration: none;
    border: none;
    margin: 2rem;
    border-radius: 1rem;
    padding: 1rem;
    color: black;
    background: #009fe3;
    :hover {
        background: rgb(222, 130, 44);
    }
`;

const Eventlist = () => {

    const [ events, setEvents ] = useState([])
    const [ filtered, setFiltered ] = useState([])

    useEffect(() => {
        fetch("/get_events")
        .then(res => res.json())
        .then(data => {
            setEvents(data)
            setFiltered(data)
        })
        .catch(err => console.error(err))
    }, [])

    function deleteEvent(e) {
        const id = e.target.name
        fetch("/delete_event", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({id})
        })
        events.forEach((e, index) => {
            if (e.id == id) {
                events.splice(index, 1)
                setEvents([...events])
                setFiltered([...events])
            }
        });
    }

    function Searchbar(e) {
        const search = events.filter(elem => {
            const string = elem.ime
            return string.toLowerCase().includes(e.target.value) || string.toUpperCase().includes(e.target.value)
        })
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Formlink to="event_obrazac">Dodaj event +</Formlink>
            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />
            <Alldiv>
            {filtered && filtered.length ? filtered.map(event => {
                return (
                    <Eventdiv key={event.id}>
                        <Styledlink to={{pathname: `event/${event.id}`}}>
                            {event.ime}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{pathname: `event_obrazac/${event.id}`}}>Ažuriraj</Button>
                            <Button onClick={deleteEvent} name={event.id}>Izbriši</Button>
                        </Buttondivs>
                    </Eventdiv>
                )
            })
            :
            <Eventdiv>
                Nema eventa za prikazati...
            </Eventdiv>
        }
        </Alldiv>
        </Wrapper>
    )
};

export default Eventlist;
