import { useState, useEffect } from "react";
import { useParams } from "react-router-dom"
import styled from "styled-components";

const Formstyled = styled.form`
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    font-size: 4rem;
`;

const Razina = styled.div`
    display: flex;
    flex-direction: column;
    border: solid black 0.1rem;
    padding: 3rem;
    font-size: 4rem;
`;

const Razinainp = styled.div`
    display: flex;
    align-items: center;
    font-size: 3rem;
`;

const Inputrad = styled.input`
    margin-left: 1rem;
`;

const UserForm = () => {

    const { id } = useParams()

    const razine = ["Predsjednik", "Potpredsjednik", "Tajnik", 
                    "Koordinator", "Aktivni Član", "Pauzirani Član"]

    const timovi = [ "HR tim", "Party tim", "Partnership tim", "Social Inclusion tim", "HealthAndWellbeing tim", 
                     "ICE tim", "Buddy tim", "PR tim", "Travel tim", "Grant tim" ]

    const spolovi = [ "Muškarac", "Žena", "Drugo" ]

    const [ userinfo, setUserinfo ] = useState({
        ime: "",
        prezime: "",
        datum: "",
        spol: "",
        razina: "",
        email: "",
        tel: "",
        tim: ""
    })

    const { ime, prezime, datum, spol, razina, email, tel, tim } = userinfo

    useEffect(() => {
        if (id) {
            fetch("/get_userform", {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ id }),
              })
            .then(res => res.json())
            .then(data => setUserinfo(data))
            .catch(err => console.error(err))
        }
    }, [])

    function handleBox(e) {
        setUserinfo(prevInfo => {
            return { ...prevInfo, [e.target.name]: e.target.value}
        })
    }
    
    function sendUserinfo(e) {
        e.preventDefault()
        if (id) {
            fetch("/update_user", {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(userinfo),
              })
              .catch(err => console.error(err))
        } else {
            fetch("/create_user", {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(userinfo),
              })
              .catch(err => console.error(err))
        }
        window.location.href = "/" 
    }

    return (
        <Formstyled onSubmit={sendUserinfo}>
            <h1>Obrazac za člana</h1>
            <div>
                <label htmlFor="ime">Ime:</label>
                <input onChange={handleBox} type="text" id="ime" name="ime" value={ime}/>

                <label htmlFor="prezime">Prezime:</label>
                <input onChange={handleBox} type="text" id="prezime" name="prezime" value={prezime}/>
            </div>

            <div>
                <label htmlFor="tel">Broj mobitela:</label>
                <input onChange={handleBox} type="tel" id="tel" name="tel" value={tel}/>
        
                <label htmlFor="email">Email:</label>
                <input onChange={handleBox} type="email" id="email" name="email" value={email}/>
            </div>	

            <div>
                <label htmlFor="datum">Datum rođenja:</label>
                <input onChange={handleBox} type="date" id="datum" name="datum" value={datum}/>
            </div>

            <Razina onChange={handleBox}>
            <p>Odaberi spol:</p>
                {spolovi.map(s => {
                    return ( 
                        <Razinainp>
                            <label htmlFor={s}>{s}</label>
                            <Inputrad checked={spol == s} type="radio" name="spol" id={s} value={s}/>
                        </Razinainp>
                    )
                })}
            </Razina>

            <div>
                <label htmlFor="tim">Odaberi tim::</label>

                <select value={tim || "default"} onChange={handleBox} name="tim" id="tim">
                    <option value="default" disabled> -- odaberi opciju -- </option>
                    {timovi.map(tim => <option value={tim}>{tim}</option> )}
                </select>
            </div>

            <Razina onChange={handleBox}>
            <p>Razina Članstva:</p>
                {razine.map(r => {
                    return (
                        <Razinainp>
                            <label htmlFor={r}>{r}</label>
                            <Inputrad checked={razina == r} type="radio" name="razina" id={r} value={r}/>
                        </Razinainp>
                    )
                })}
            </Razina>

            <input value="Spremi" type="Submit"/>
        </Formstyled>
    )
}

export default UserForm;
