import { useState } from "react";
import styled from "styled-components"
import esnlogo from "./image3.png"

const Wrapper = styled.div`
  	display: flex;
    min-height: 100vh;    
    background: linear-gradient(to right top, rgb(255, 255, 255), rgb(86, 232, 132));
    justify-content: space-around;
    align-items: center;
    @media (max-width: 726px) {
      flex-direction: column;
    }
	position: relative;
`;

const Image = styled.img`
  	object-fit: contain;
    height: 15rem;
    width: 30rem;
	z-index: 1;
`;

const LoginForm = styled.form`
    display: flex;
    padding: 1em;
    flex-direction: column;
    align-items: center;
    justify-content: center;
	z-index: 1;
`;

const Label = styled.label`
    font-size: 3.5rem;
    padding: 0.5rem;
`;

const Input = styled.input`
    font-size: 1.7rem;
    padding: 0.5rem;
    border: none;
    border-bottom: 0.3rem solid rgb(245, 101, 29);
    outline: none;
    :focus {
      border-bottom: 0.2rem solid black
    }
`;

const Button = styled.input`
    font-size: 2.3rem;
    border-radius: 1rem;
    background: whitesmoke;
    margin: 1rem;
    padding: 0.5rem;
`;

const ErrorMessage = styled.p`
    font-size: 2.5rem;
    color: red;
`;

const Baloon = styled.div`
	border-radius: 50%;
	background: linear-gradient(to right top, rgb(255, 255, 255), rgba(86, 232, 132, 0.5));
	height: 15rem;
	width: 15rem;
	position: absolute;
`;

const Baloonright = styled(Baloon)`
	top: 25%;
	right: 25%;
`;

const Baloonleft = styled(Baloon)`
	bottom: 25%;
	left: 25%;
`;

const Login = () => {

  const [loginfo, setLoginfo] = useState({ username: "", password: "" });
  const [errorinfo, setErrorinfo] = useState();

  function sendLoginfo(e) {
    e.preventDefault()
    fetch("/auth/login", {
      method: "POST",
      mode: "cors",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(loginfo),
    })
      .then((res) => res.json())
      .then((res) => {
        if (res === loginfo.username) {
          localStorage.setItem("user", res);
          window.location.href = "/";
        } else {
          setLoginfo({ username: "", password: "" });
          setErrorinfo(res.message);
        }
      })
      .catch((error) => console.log(error));
  }

  function handleInput(e) {
    const { name, value } = e.target;
    setLoginfo((prevInfo) => {
        return { ...prevInfo, [name]: value };
    });
  }

  return (
    <Wrapper>
		<Baloonright />
	  	<Baloonleft />
      	<Image src={esnlogo}></Image>
    	<LoginForm onSubmit={sendLoginfo}>
    	  <Label htmlFor="username">Korisničko ime:</Label>
    	  <Input
    	    id="username"
    	    type="text"
    	    name="username"
    	    onChange={handleInput}
    	    value={loginfo.username}
    	  />
    	  <Label htmlFor="password">Šifra:</Label>
    	  <Input
    	    id="password"
    	    type="password"
    	    name="password"
    	    onChange={handleInput}
    	    value={loginfo.password}
    	  />
    	  <Button value="Prijavi se" type="submit" />
    	  <ErrorMessage>{errorinfo ? `${errorinfo}*` : ""}</ErrorMessage>
    	</LoginForm>
    </Wrapper>
  );
};

export default Login;
