import { Link } from "react-router-dom"
import styled from "styled-components"

const Wrapper = styled.div`
    font-size: 5rem;
    flex: 1;
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: center;
    background: linear-gradient(to right top, rgba(179, 255, 171, 0.1), rgba(18, 255, 24, 0.2));
`;

const Styledlink = styled(Link)`
    flex-basis: 50%;
    text-align: center;
    padding: 2rem;
    text-decoration: none;
    color: black;
`;

const Svitimovi = () => {

    const timovi = [ "HR tim", "Party tim", "Partnership tim", "Social Inclusion tim", "HealthAndWellbeing tim", 
                     "ICE tim", "Buddy tim", "PR tim", "Travel tim", "Grant tim" ]

    return (
        <Wrapper>
            {timovi.map(tim => {
                return (
                    <Styledlink 
                        key={tim}
                        to={{
                            pathname: `/tim/${tim}`
                        }}
                    >
                        {tim}
                    </Styledlink>
                )
            })}
        </Wrapper>
    )
}

export default Svitimovi;
